import { createCustomElement } from "@seismic/core";
import { createUpdateStateEffect } from "@seismic/effect-update-state";
import snabbdom from "@seismic/snabbdom-renderer";
import _ from "lodash";
import "./sidebarAction";
import resizeBehavior from "@servicenow/behavior-resize";
import {
	createSidebarKeyBindingBehavior,
	createSidebarKeyBindingActions,
	createSidebarKeyBindingActionsHandlers
} from "./sidebarKeyBinding";
import {
	MORE_ACTION,
	SIDEBAR_TOOLBAR_INTERNAL_ACTION,
	SIDEBAR_INTERNAL_ACTION
} from "../constants/constants";
import "@servicenow/now-button";
import "../menu/sidebarMenu";
import styles from "./sidebarToolbar.scss";
import "@servicenow/now-legacy-icon";

const BUTTON_HEIGHT = 40;
const isSelectMore = (moreActions, selectedAction) =>
	moreActions.findIndex(a => a.name === selectedAction) > -1;

const transformState = (state, dispatch) => {
	const {
		properties: { actions },
		behaviors: {
			resize: { size }
		}
	} = state;
	const { height } = size;
	if (!height) return;

	// fitHeight: height that will fit the number of buttons without the empty space so we'll use that to set the height of the internal div that hold actions.
	// maxActions: maximum number of action buttons that can fit in the fitHeight.
	// If the maxActions is less than the number of actions we need to show, those don't fit in the space will be push to the overflow, and the overflow button will be shown.
	const fitHeight = height - (height % BUTTON_HEIGHT);
	const maxActions = fitHeight ? fitHeight / BUTTON_HEIGHT : actions.length;
	const hasMore = actions.length > maxActions;
	const visibleActions = hasMore ? actions.slice(0, maxActions - 1) : actions;
	const moreActions = hasMore ? actions.slice(maxActions - 1) : [];

	dispatch(SIDEBAR_TOOLBAR_INTERNAL_ACTION.UPDATE_STATE, {
		local: {
			...state.local,
			hasMore,
			visibleActions,
			moreActions,
			fitHeight,
			height
		}
	});
};

const view = (state, dispatch) => {
	const {
		properties: { actions = [], selectedAction, openedMore, focusOnSidebar },
		local: {
			hasMore,
			visibleActions = actions,
			moreActions = [],
			fitHeight,
			height
		},
		behaviors: { resize = {} },
		componentId
	} = state;

	const focusIfNeeded = (oldNode, newNode) => {
		if (newNode.data.props.shouldFocus && focusOnSidebar) {
			const focusEl = newNode.elm.shadowRoot
				? newNode.elm.shadowRoot.querySelector("button")
				: newNode.elm;
			if (focusEl) focusEl.focus();
		}
	};

	if (resize.size.height !== height) {
		transformState(state, dispatch);
	}
	const inlineStyle = fitHeight
		? {
				height: `${fitHeight}px;`
		  }
		: {};
	const menuStyle = fitHeight
		? {
				bottom: `${height - fitHeight}px;`
		  }
		: {};

	const isSelectedInMenu =
		hasMore && selectedAction
			? isSelectMore(moreActions, selectedAction)
			: false;

	const setTargetRef = ref =>
		dispatch(SIDEBAR_TOOLBAR_INTERNAL_ACTION.UPDATE_STATE, { targetRef: ref });

	const onToolbarFocus = (state, dispatch) => {
		return event => {
			const focusCameFrom = !event.relatedTarget
				? "unknown"
				: event.relatedTarget.tagName.toLowerCase();
			if (
				focusCameFrom.indexOf("sidebar") === -1 ||
				focusCameFrom === "sn-form-internal-sidebar-panel" ||
				focusCameFrom === "sn-contextual-sidebar-menu"
			) {
				if (isSelectedInMenu)
					dispatch(SIDEBAR_INTERNAL_ACTION.MORE_CLICKED, { opened: true });
				else
					dispatch(SIDEBAR_INTERNAL_ACTION.UPDATE_STATE, {
						focusOnSidebar: true
					});
			}
		};
	};

	const onToolbarBlur = (dispatch) => {
		return () => {
			dispatch(SIDEBAR_INTERNAL_ACTION.UPDATE_STATE, {
				focusOnSidebar: false
			});
		};
	};

	return (
		<div
			className="sidebar-action-wrapper"
			style={inlineStyle}
			on-focusin={onToolbarFocus(state, dispatch)}
			on-focusout={onToolbarBlur(dispatch)}
		>
			<div className="sidebar-action-container">
				{visibleActions.map((action, index) => (
					<sn-contextual-sidebar-action
						{...action}
						selected={(selectedAction === action.name).toString()}
						should-focus={selectedAction === action.name && !openedMore}
						hook={{ update: focusIfNeeded }}
						index={index}
					/>
				))}
			</div>
			{hasMore ? (
				<button
					className="sidebar-action-button"
					on-click={() => {
						dispatch(SIDEBAR_INTERNAL_ACTION.MORE_CLICKED, {
							opened: !openedMore
						});
					}}
					hidden={!hasMore}
					should-focus={openedMore && !isSelectedInMenu}
					tabindex={!openedMore && isSelectedInMenu ? "0" : "-1"}
					hook-insert={({ elm }) => setTargetRef(elm)}
					hook-destroy={() => setTargetRef(null)}
					aria-label={MORE_ACTION.tooltip}
				>
					{" "}
					<now-icon icon={MORE_ACTION.icon} size="md" />
					<now-tooltip
						target-ref={state.targetRef}
						content={MORE_ACTION.tooltip}
					/>
				</button>
			) : null}
			{openedMore ? (
				<sn-contextual-sidebar-menu
					style={menuStyle}
					actions={moreActions}
					selected-action={selectedAction}
					component-id={`${componentId}_menu`}
				/>
			) : null}
		</div>
	);
};

createCustomElement("sn-contextual-sidebar-toolbar", {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		actions: {
			default: [],
			onChange(afterProps, beforeProps, dispatch) {
				if (!_.isEqual(afterProps, beforeProps))
					dispatch(
						SIDEBAR_TOOLBAR_INTERNAL_ACTION.TRANSFORM_ACTIONS,
						afterProps
					);
			}
		},
		selectedAction: {},
		openedMore: {
			default: false
		},
		focusOnSidebar: {
			default: false
		}
	},
	initialState: {
		local: {}
	},
	behaviors: [createSidebarKeyBindingBehavior(), { behavior: resizeBehavior }],
	actions: {
		[SIDEBAR_TOOLBAR_INTERNAL_ACTION.UPDATE_STATE]: {
			private: true
		},
		[SIDEBAR_TOOLBAR_INTERNAL_ACTION.TRANSFORM_ACTIONS]: {
			private: true
		},
		...createSidebarKeyBindingActions()
	},
	actionHandlers: {
		[SIDEBAR_TOOLBAR_INTERNAL_ACTION.UPDATE_STATE]: createUpdateStateEffect(
			(state, { payload }) => ({
				...state,
				...payload
			})
		),
		[SIDEBAR_TOOLBAR_INTERNAL_ACTION.TRANSFORM_ACTIONS]: ({
			action,
			dispatch,
			state
		}) => {
			const { payload } = action;

			transformState({ ...state, properties: { actions: payload } }, dispatch);
		},
		...createSidebarKeyBindingActionsHandlers()
	},
	styles
});
