import { createCustomElement } from "@seismic/core";
import snabbdom from "@seismic/snabbdom-renderer";
import "./sidebarMenuItem";
import styles from "./sidebarMenu.scss";

const focusNodeIfNeeded = node => {
	if (node.data.props.shouldFocus) {
		const focusEl = node.elm.shadowRoot
			? node.elm.shadowRoot.querySelector("div")
			: node.elm;
		if (focusEl) focusEl.focus();
	}
};

const focusIfNeededUpdate = (oldNode, newNode) => focusNodeIfNeeded(newNode);

const focusIfNeededInsert = newNode => {
	window.requestAnimationFrame(() => {
		focusNodeIfNeeded(newNode);
	});
};

const view = (state, dispatch) => {
	const {
		properties: { actions, selectedAction },
		componentId
	} = state;

	return (
		<div>
			{actions.map((action, index) => (
				<sn-contextual-sidebar-menu-item
					{...action}
					selected={(selectedAction === action.name).toString()}
					should-focus={selectedAction === action.name}
					hook={{ update: focusIfNeededUpdate, insert: focusIfNeededInsert }}
					index={index}>
				</sn-contextual-sidebar-menu-item>
			))}
		</div>
	);
};

createCustomElement("sn-contextual-sidebar-menu", {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		actions: { default: [] },
		selectedAction: { default: {} }
	},
	styles
});
