import { createCustomElement } from "@seismic/core";
import { createUpdateStateEffect } from "@seismic/effect-update-state";
import snabbdom from "@seismic/snabbdom-renderer";
import "@servicenow/now-button";
import {
	SIDEBAR_INTERNAL_ACTION,
	SIDEBAR_ACTION_INTERNAL_ACTION
} from "../constants/constants";
import styles from "./sidebarAction.scss";
import "@servicenow/now-tooltip";

const view = (state, dispatch) => {
	const {
		properties: { icon, name, selected, tooltip }
	} = state;
	const variant = selected
		? "sidebar-action-button is-selected"
		: "sidebar-action-button";

	const setTargetRef = ref =>
		dispatch(SIDEBAR_ACTION_INTERNAL_ACTION.UPDATE_STATE, { targetRef: ref });

	return (
		<div>
			<button
				type="button"
				className={variant}
				on-click={event => {
					event.preventDefault();
					event.stopPropagation();
					dispatch(SIDEBAR_INTERNAL_ACTION.ACTION_CLICKED, { name });
				}}
				hook-insert={({ elm }) => setTargetRef(elm)}
				hook-remove={() => setTargetRef(null)}
				aria-label={tooltip}
			>
				{" "}
				<now-icon icon={icon} size="md" />
			</button>
			<now-tooltip target-ref={state.targetRef} content={tooltip} />
		</div>
	);
};

createCustomElement("sn-contextual-sidebar-action", {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		name: {
			reflect: true
		},
		icon: {
			reflect: true
		},
		selected: {
			reflect: true,
			default: false,
			unstableParse: true
		},
		tooltip: {}
	},
	actions: {
		[SIDEBAR_ACTION_INTERNAL_ACTION.UPDATE_STATE]: {
			private: true
		}
	},
	actionHandlers: {
		[SIDEBAR_ACTION_INTERNAL_ACTION.UPDATE_STATE]: createUpdateStateEffect(
			(state, action) => ({
				...state,
				...action.payload
			})
		)
	}
});
