import keyBindingBehavior from "@servicenow/behavior-key-binding";
import { SIDEBAR_INTERNAL_ACTION } from "../constants/constants";

const ARROW_KEY_PRESSED_ACTION = "ARROW_KEY_PRESSED_ACTION";
const ENTER_KEY_PRESSED_ACTION = "ENTER_KEY_PRESSED_ACTION";

const getActiveElement = el => {
	return el.shadowRoot && el.shadowRoot.activeElement
		? getActiveElement(el.shadowRoot.activeElement)
		: el;
};

const getActiveAction = el => {
	return el.shadowRoot && el.shadowRoot.activeElement && el.index === undefined
		? getActiveAction(el.shadowRoot.activeElement)
		: el;
};

const getActiveActionIndex = el => {
	const activeElement = getActiveAction(el);
	return activeElement && activeElement.index !== undefined
		? activeElement.index
		: -1;
};

const nextActionIndex = (actions, index) =>
	index + 1 > actions.length - 1 ? 0 : index + 1;

const prevActionIndex = (actions, index) =>
	index - 1 >= 0 ? index - 1 : actions.length - 1;

const selectAction = findActionFn => (state, dispatch, target) => {
	const {
		properties: { selectedAction, actions },
		local: { visibleActions = actions, hasMore = false }
	} = state;
	const index = actions.findIndex(a => a.name === selectedAction);
	const focusIndex = index === -1 ? getActiveActionIndex(target) : index;
	const newIndex = findActionFn(
		actions,
		focusIndex === undefined ? -1 : focusIndex
	);

	if (index === newIndex) return;

	if (hasMore && newIndex === visibleActions.length && newIndex > index)
		dispatch(SIDEBAR_INTERNAL_ACTION.MORE_CLICKED, { opened: true });

	const payload = {
		name: actions[newIndex].name,
		...(newIndex >= visibleActions.length ? { openedMore: true } : {})
	};
	dispatch(SIDEBAR_INTERNAL_ACTION.ACTION_CLICKED, payload);
};

const nextAction = selectAction(nextActionIndex);
const prevAction = selectAction(prevActionIndex);
const isFocusOnMoreButton = target =>
	target ? getActiveElement(target).classList.contains("more-button") : false;
const openMore = (target, moreActions, dispatch) => {
	if (isFocusOnMoreButton(target)) {
		dispatch(SIDEBAR_INTERNAL_ACTION.ACTION_CLICKED, {
			name: moreActions[0].name,
			openedMore: true
		});
		return true;
	}
	return false;
};

const focusFirstOrLastAction = (host, getLast = false, fromMenu = false) => {
	if (!host.shadowRoot) return;
	const action = host.shadowRoot.querySelector(
		`${
			fromMenu
				? "sn-contextual-sidebar-menu-item"
				: "sn-form-internal-sidebar-action"
		}${getLast ? ":last-child" : ""}`
	);
	if (!action || !action.shadowRoot) return;
	action.shadowRoot.querySelector(fromMenu ? "div" : "button").focus();
};

const focusFirstAction = host => focusFirstOrLastAction(host, false);
const focusLastAction = host => focusFirstOrLastAction(host, true);
const focusLastMenuAction = host => focusFirstOrLastAction(host, true, true);

const focusLastActionOrMoreButton = host => {
	if (!host.shadowRoot) return;
	const menu = host.shadowRoot.querySelector("sn-contextual-sidebar-menu");
	if (menu) {
		focusLastMenuAction(menu);
	} else {
		const moreButton = host.shadowRoot.querySelector(".sidebar-action-button");
		if (moreButton && !moreButton.hidden) moreButton.focus();
		else focusLastAction(host);
	}
};

export const createSidebarKeyBindingBehavior = () => {
	return {
		behavior: keyBindingBehavior,
		options: {
			keyBindings: {
				onEnterKeyPress: "Enter",
				onArrowKeyPress: ["ArrowUp", "ArrowDown"],
				onTabKeyPress: ["Tab"]
			},
			handlers: {
				onArrowKeyPress: function(host, event, dispatch) {
					if (event.ctrlKey || event.shiftKey) return true;
					dispatch(ARROW_KEY_PRESSED_ACTION, {
						target: event.target,
						code: event.code || event.key
					});
					return false;
				},
				onEnterKeyPress: function(host, event, dispatch) {
					dispatch(ENTER_KEY_PRESSED_ACTION, {
						target: event.target,
						code: event.code
					});
					return false;
				},
				onTabKeyPress: function(host, event) {
					if (event.shiftKey) {
						focusFirstAction(host);
					} else {
						focusLastActionOrMoreButton(host);
					}
					return false;
				}
			}
		}
	};
};

export const createSidebarKeyBindingActionsHandlers = () => {
	return {
		[ARROW_KEY_PRESSED_ACTION]: ({ action, state, dispatch }) => {
			const {
				properties: { openedMore },
				local: { moreActions }
			} = state;
			const { target, code } = action.payload;

			switch (code) {
				case "Down":
				case "ArrowDown": {
					openMore(target, moreActions, dispatch) ||
						nextAction(state, dispatch, target);
					return;
				}
				case "Up":
				case "ArrowUp": {
					prevAction(state, dispatch, target);
					return;
				}
			}
		},
		[ENTER_KEY_PRESSED_ACTION]: ({ action, state, dispatch }) => {
			const {
				properties: { actions, openedMore },
				local: { moreActions }
			} = state;
			const { target } = action.payload;

			if (isFocusOnMoreButton(target)) {
				dispatch(SIDEBAR_INTERNAL_ACTION.MORE_CLICKED, { opened: !openedMore });
				return;
			}
			const focusIndex = getActiveElementIndex(target);
			const focusedAction = openedMore
				? moreActions[focusIndex]
				: actions[focusIndex];
			if (focusedAction) {
				dispatch(SIDEBAR_INTERNAL_ACTION.MORE_CLICKED, {
					name: focusedAction.name
				});
				return;
			}
		}
	};
};

export const createSidebarKeyBindingActions = () => {
	return {
		[ARROW_KEY_PRESSED_ACTION]: {
			private: true
		},
		[ENTER_KEY_PRESSED_ACTION]: {
			private: true
		}
	};
};
