import {createCustomElement, actionTypes} from '@servicenow/ui-core';
const {COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPED} = actionTypes;
import { createUpdateStateEffect } from "@seismic/effect-update-state";

import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {uxfViewportBehavior} from '@devsnc/library-uxf';
import keys from "lodash/keys";

import {
	SIDEBAR_ACTION,
	SIDEBAR_INTERNAL_ACTION
} from "./constants/constants";

import '@servicenow/now-empty-state';
import styles from './nowContextualSidebar.scss';
import './toolbar/sidebarToolbar';

const getDefaultRoute = (viewportRoutes, incomingDefaultRoute, actions) => {
	if (incomingDefaultRoute && viewportRoutes[incomingDefaultRoute]) {
		return incomingDefaultRoute;
	}

	return actions[0].name;
}

const view = (state, {updateState, dispatch}) => {
	const {
		focusOnSidebar,
		openedMore,
		isHidden,
		initialized,
		properties: {
			defaultRoute: incomingDefaultRoute,
			// Below properties are set by the uxfViewportBehavior
			viewportRoutes,
			activeRoute,
			collapsable
		}
	} = state;
	
	if (!viewportRoutes || keys(viewportRoutes).length === 0) {
		return (
			<now-empty-state 
				type="not-configured" 
				variant="small" />
		);
	}

	const actions = keys(viewportRoutes)
		.map(routePath => {
			return {
				routePath,
				route: viewportRoutes[routePath][0]
			};
		})
		.filter(({ route }) => {
			return (!route.fields || route.fields.length === 0) && (!route.optionalParameters || route.optionalParameters.length === 0);
		})
		.sort((firstEl, secondEl) => {
			return firstEl.route.order - secondEl.route.order;
		})
		.map(({ routePath, route }) => {
			return {
				icon: route.icon || "circle-outline",
				label: route.name,
				name: routePath,
				order: route.order,
				tooltip: route.name
			};
		});

	const defaultRoute = getDefaultRoute(viewportRoutes, incomingDefaultRoute, actions);

	if (!initialized) {
		if (!activeRoute) {
			dispatch("VIEWPORT_ROUTE_REQUESTED", {
				route: defaultRoute
			});
		}

		updateState({initialized: true});
	}

	const selectedAction = (activeRoute) ? activeRoute.routeType : defaultRoute;

	return (
		<div className="sidebar">
			<sn-contextual-sidebar-toolbar
					actions={actions}
					focus-on-sidebar={focusOnSidebar}
					opened-more={openedMore}
					selected-action={selectedAction}
					on-blur={() => {
						updateState({ openedMore: false });
					}}
				/>
	
			<div className={`sidebar-panel ${!selectedAction || (isHidden && collapsable) ? "is-hidden" : ""}`}>
				<slot name="viewport">
					<p>Loading...</p>
				</slot>
			</div>
		</div>
	);
}

createCustomElement('now-contextual-sidebar', {
	renderer: {type: snabbdom},
	view,
	styles,
	properties: {
		defaultRoute: {},
		collapsable: {
			default: false,
			schema: {
				type: "boolean"
			}
		}
	},
	initialState: {
		focusOnSidebar: false,
		isHidden: false,
		initialized: false
	},
	behaviors: [uxfViewportBehavior],
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: ({ action, dispatch }) => {
			const { name, previousValue = {}, value = {} } = action.payload;
			if (name === 'activeRoute') {
				if (value.routeType !== previousValue.routeType) {
					dispatch(SIDEBAR_ACTION.ROUTE_CHANGED, {
						name: value.routeType
					});
				}
			}
		},
		[SIDEBAR_INTERNAL_ACTION.ACTION_CLICKED]: ({
			state,
			properties,
			action,
			updateState,
			dispatch
		}) => {
			const {
				payload: { name, openedMore }
			} = action;

			const {activeRoute, collapsable} = properties;

			let isHidden = state.isHidden;

			if (activeRoute) {
				if (activeRoute.routeType !== name) {
					dispatch("VIEWPORT_ROUTE_REQUESTED", {
						route: name
					});
					if(isHidden) {
						isHidden = false;
						if (collapsable)
							dispatch(SIDEBAR_ACTION.PANEL_TOGGLED, {
								isHidden
							});
					}
				} else  {
					isHidden = !isHidden;
					if (collapsable)
						dispatch(SIDEBAR_ACTION.PANEL_TOGGLED, {
							isHidden
						});
				}
			}

			dispatch(SIDEBAR_ACTION.ACTION_CLICKED, {
				name
			});

			updateState({
				openedMore: !!openedMore,
				focusOnSidebar: true,
				isHidden
			});
		},
		[SIDEBAR_INTERNAL_ACTION.MORE_CLICKED]: createUpdateStateEffect(
			(state, { payload: { opened } }) => ({
				...state,
				openedMore: opened
			})
		),
		[SIDEBAR_INTERNAL_ACTION.UPDATE_STATE]: createUpdateStateEffect(
			(state, action) => ({
				...state,
				...action.payload
			})
		)
	}
});
