import { createCustomElement } from "@seismic/core";
import snabbdom from "@seismic/snabbdom-renderer";
import "@servicenow/now-button";
import "@servicenow/now-tooltip";
import "@servicenow/now-legacy-icon";
import { SIDEBAR_INTERNAL_ACTION } from "../constants/constants";
import styles from "./sidebarMenuItem.scss";

const view = (state, dispatch) => {
	const {
		properties: { icon, name, label, selected, tooltip, targetRef }
	} = state;

	const setTargetRef = ref => {
		dispatch("PROPERTIES_SET", { targetRef: ref });
	};
	const tooltipId = `tooltip-${name}`;

	return (
		<div
			hook-insert={({ elm }) => setTargetRef(elm)}
			hook-remove={() => setTargetRef(null)}
			on-click={() =>
				dispatch(SIDEBAR_INTERNAL_ACTION.ACTION_CLICKED, { name })
			}
			class={{ "sidebar-toolbar-menu-item": true, "-selected": selected }}
			tabIndex="0"
			aria-label={tooltip}
		>
			<now-icon
				className="sidebar-toolbar-menu-item-icon"
				icon={icon}
				size="md"
				weight="bold"
			/>
			<span
				className="sidebar-toolbar-menu-item-label"
				on-mouseover={({ target }) => {
					if (target.offsetWidth < target.scrollWidth) {
						target.title = label;
					}
				}}
			>
				{label}
			</span>
			{tooltip ? (
				<now-tooltip id={tooltipId} target-ref={targetRef} content={tooltip} />
			) : null}
		</div>
	);
};

createCustomElement("sn-contextual-sidebar-menu-item", {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		targetRef: {
			default: null
		},
		name: {
			reflect: true
		},
		label: {
			reflect: true
		},
		icon: {
			reflect: true
		},
		selected: {
			reflect: true,
			default: false
		},
		tooltip: {}
	},
	styles
});
